:root {

    /** ---------------------------
     * Fundamentals
     --------------------------- */
    --ls-spacing: 1;
    --ls-radius: 1;
    
    --ls-color-light: #efefef;
    --ls-color-dark: #151515;
    
    --ls-color-primary: #4e42d9;
    --ls-color-secondary: #1f1f1f;

    /** ---------------------------
     * Basic CP color.
     --------------------------- */

    --ls-color-black: #000;
    --ls-color-white: #fff;
    --ls-color-gray: #6B7280;

    --ls-color-red: #EF4444;
    --ls-color-yellow: #F59E0B;
    --ls-color-green: #10B981;
    --ls-color-blue: #3B82F6;
    
    --ls-screen-sm: 640px;
    --ls-screen-md: 768px;
    --ls-screen-lg: 1024px;
    --ls-screen-xl: 1280px;
    --ls-screen-2xl: 1536px;

    --ls-font-sans: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    --ls-font-serif: Georgia, Cambria, "Times New Roman", Times, serif;
    --ls-font-mono: Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}
